/**
 * headlines with line.
 */
h1.line:before, .line.h1:before,
h1.line:after,
.line.h1:after,
h2.line:before,
.line.h2:before,
h2.line:after,
.line.h2:after,
h3.line:before,
.line.h3:before,
h3.line:after,
.line.h3:after,
h4.line:before,
.line.h4:before,
h4.line:after,
.line.h4:after {
  background-color: #454545;
  content: "";
  display: inline-block;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 50%;
}

h1.line:before, .line.h1:before,
h2.line:before,
.line.h2:before,
h3.line:before,
.line.h3:before,
h4.line:before,
.line.h4:before,
h1.entry-title:before,
.entry-title.h1:before,
h2.entry-title:before,
.entry-title.h2:before {
  right: 0.5em;
  margin-left: -50%;
}

h1.line:after, .line.h1:after,
h2.line:after,
.line.h2:after,
h3.line:after,
.line.h3:after,
h4.line:after,
.line.h4:after,
h1.entry-title:after,
.entry-title.h1:after,
h2.entry-title:after,
.entry-title.h2:after {
  left: 0.5em;
  margin-right: -50%;
}

h1.line, .line.h1,
h2.line,
.line.h2,
h3.line,
.line.h3,
h4.line,
.line.h4,
h1.entry-title,
.entry-title.h1,
h2.entry-title,
.entry-title.h2 {
  overflow: hidden;
  text-align: center;
  text-transform: uppercase;
  line-height: 1.25em;
  width: 100%;
}

