/**
 * Menu Experiments.
 */
#wrapper-navbar > nav > div.container {
  justify-content: center;
}

@media (min-width: 992px) {
  .navbar-nav {
    flex-flow: row !important;
  }
}
.navbar-nav li {
  flex-flow: row;
  padding: 0.25rem 1rem;
}

.dropdown-menu {
  background-color: unset !important;
  color: white !important;
}

#main-menu a {
  background-color: unset !important;
  color: white !important;
}

.navbar button {
  color: #000;
  background: none;
  border: none;
  font-size: 1em !important;
  font-family: "Raleway";
  font-weight: 600;
  padding: 0.5rem !important;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

/**
 * Forces sub menu's
 * to display when menu
 * is displayed!
 */
@media (min-width: 992px) {
  ul.dropdown-menu {
    display: inline-grid;
  }
}
/**
 * Targets specific menu item
 * and makes it a column
 * (for social icons).
 * 
 * (Currently aims at id
 * can be improved)
 */
/*mobile version*/
#menu-item-10920 > ul.show {
  grid-auto-flow: column;
  display: inline-grid;
  padding: 0.25rem 1rem;
}

/*pc version*/
@media (min-width: 992px) {
  #menu-item-10920 > ul {
    grid-auto-flow: column;
    display: inline-grid;
    padding: 0.25rem 1rem;
  }
}
/*remove padding*/
#menu-item-10920 > ul > li {
  padding: unset !important;
}

#menu-item-10920 > ul > li > a {
  padding: unset !important;
}

/**
 * Removes menu arrows.
 */
.dropdown-toggle:after {
  display: none !important;
}

/**
 * Forcing Left Align
 * in Menus.
 */
.menu-item {
  text-align: left !important;
}

.menu-item ul {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.menu-item li {
  padding-left: 0px;
  padding-right: 0px;
}

.menu-item a {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

/**
 * Left Border Lines
 * in Menus.
 */
.dropdown-menu {
  border-left-color: #fff !important;
  border-left-style: solid !important;
  border-left-width: thin !important;
  border-radius: 0px !important;
}

/**
 * Padding stuff
 * (probably overwriting
 * redundant stlying, so
 * go back and check later).
 */
.dropdown-menu > * {
  padding-left: 10px !important;
}

#menu-item-10920 > ul {
  padding-left: 10px !important;
}

.dropdown-menu > * {
  padding-top: 0px !important;
}

.dropdown-menu > * > * {
  padding-top: 0px !important;
}

.navbar-nav > * {
  padding-bottom: 1rem !important;
}

.dropdown-menu > :nth-last-child(1) {
  padding-bottom: 0px !important;
}

.dropdown-menu > :nth-last-child(1) > * {
  padding-bottom: 0px !important;
}

.dropdown-toggle.nav-link {
  font-weight: bold !important;
}

/**
 * Makes menu word
 * wrap differently.
 * 
 * (Disabled)
 */
.dropdown-item {
  white-space: pre-wrap !important;
}

/**
 * Disable Clicking on Menu
 * Top Item (Except on mobile).
 */
@media (min-width: 992px) {
  .navbar-nav > .menu-item > a {
    pointer-events: none !important;
  }
}
/**
 * Footer stuff.
 */
.site-info p {
  margin-bottom: 0px;
}

.footer-extra p {
  margin-bottom: 0px;
}

.footer-extra {
  margin-top: 1rem;
}

/**
 * Left Border Lines
 * in Menus.
 */
#footer-full-content > .row > :nth-last-child(1) > .wp-block-group {
  border-left-color: #fff !important;
  border-left-style: solid !important;
  border-left-width: medium !important;
  border-radius: 0px !important;
  padding-left: 1rem;
}

@media (min-width: 768px) {
  #footer-full-content > .row > :first-child {
    flex: 0 0 auto;
    width: 70% !important;
  }
  #footer-full-content > .row > :nth-last-child(1) {
    flex: 0 0 auto;
    width: 30% !important;
  }
}
@media (min-width: 768px) {
  #footer-full-content > .row > :first-child > .wp-block-group {
    text-align: left;
    width: 450px;
  }
  #footer-full-content > .row > :first-child .wp-block-image > img {
    width: inherit;
  }
}
#footer-full-content > .row > :first-child > .wp-block-group p {
  border-left-color: #fff !important;
  border-left-style: solid !important;
  border-left-width: medium !important;
  border-radius: 0px !important;
  padding-left: 1rem;
  margin-top: 1rem !important;
}

.socialiconz > :nth-last-child(1) {
  padding-left: 0px;
}

/**
 * Auto sets the height of the
 * accessability box.
 */
#ufw_1 .ufw_wb {
  height: auto !important;
}

/**
 * Prevents accessability box
 * filling phone screen.
 */
@media screen and (max-width: 600px) {
  .ufw_pp .ufw_wb {
    top: unset !important;
  }
}
/**
 * Feature Page
 * Styling.
 */
.feature-logo {
  margin-left: 5% !important;
  width: 20%;
  height: 20%;
}

#innerdiv1 {
  width: 100%;
  top: 0;
}

#innerdiv2 .h1, #innerdiv2 h1 {
  font-size: 2.5em;
}

#innerdiv2 {
  bottom: 0;
  margin-left: 5% !important;
}

body.page-template-featurepage .entry-title.h1, body.page-template-featurepage h1.entry-title {
  background-color: #000 !important;
  border-radius: 15px 15px 0 0;
  padding-left: 10px;
  padding-right: 10px;
  text-transform: unset;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}

/**
 * Styling special links
 * that should not follow the
 * standard colour scheme.
 */
.special a:-webkit-any-link {
  color: unset;
}

.special a:-webkit-any-link:hover {
  color: #0571ff;
}

/**
 * Centers menu buttons
 * on mobile.
 */
@media (max-width: 991px) {
  #buttonz {
    justify-content: center !important;
  }
}
/**
 * Media Text Block Default Padding Removed.
 * Only if .padded class is not applied.
 */
.wp-block-media-text:not(.padded).has-media-on-the-right .wp-block-media-text__content {
  padding-left: 0 !important;
}

.wp-block-media-text:not(.padded):not(.has-media-on-the-right) .wp-block-media-text__content {
  padding-right: 0 !important;
}

@media screen and (max-width: 600px) {
  .wp-block-media-text:not(.padded):is(.has-media-on-the-right):is(.is-stacked-on-mobile) > div.wp-block-media-text__content {
    padding-right: 0 !important;
  }
  .wp-block-media-text:not(.padded):not(.has-media-on-the-right):is(.is-stacked-on-mobile) > .wp-block-media-text__content {
    padding-left: 0 !important;
  }
}
/**
 * Reduces column gap to 25px.
 */
:where(.wp-block-columns.is-layout-flex) {
  column-gap: 25px !important;
}

/**
 * Forces the right column to
 * stack on top of the left
 * on mobile.
 */
@media (max-width: 781px) {
  .wp-block-columns.reverse:not(.is-not-stacked-on-mobile) {
    flex-flow: column-reverse !important;
  }
}
/**
 * Adds padding to a column.
 */
.wp-block-column .padded {
  padding: 8% 8% !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

